<template>
  <div class="quantity">
    <button class="btn quantity-minus" @click="QuantityMinus">-</button>
    <input type="number" class="quantity-text" v-model.number="inputQuantity" @change="QuantityInput" />
    <button class="btn quantity-plus" @click="QuantityPlus">+</button>
  </div>
</template>

<script>
export default {
  name: 'ButtonQuantity',
  props: {
    quantity: Number,
  },
  data() {
    return {
      inputQuantity: 1,
    }
  },
  methods: {
    QuantityInput() {
      if (this.inputQuantity > 100) this.inputQuantity = 100
      else if (this.inputQuantity < 0) this.inputQuantity = 0

      this.inputQuantity = Math.floor(this.inputQuantity)
      this.$emit('inputQuantity', this.inputQuantity)
    },
    QuantityPlus() {
      this.inputQuantity++
      if (this.inputQuantity > 100) this.inputQuantity = 100
      this.$emit('inputQuantity', this.inputQuantity)
    },
    QuantityMinus() {
      if (this.inputQuantity > 0) {
        this.inputQuantity--
        this.$emit('inputQuantity', this.inputQuantity)
      }
    },
    QuantityReset() {
      this.inputQuantity = 1
      this.$emit('inputQuantity', this.inputQuantity)
    },
  },
  mounted() {
    if (this.quantity !== undefined && !isNaN(this.quantity)) {
      if (this.quantity !== 0) {
        this.inputQuantity = this.quantity
      }
    } else {
      this.inputQuantity = 1
    }
  },
}
</script>

<style lang="scss">
.quantity {
  display: flex;
  justify-content: center;
  align-items: center;
  .quantity-text {
    width: 40px;
    display: block;
    text-align: center;
    padding: 5px 0px;
    border: solid 1px $color-gray;
    outline: none;
  }
}
</style>
