<template>
  <main class="page-cart">
    <h3 class="page-title">購物清單</h3>

    <ol class="cart-list" v-if="CartList.length > 0">
      <CartItem v-for="good in CartList" :id="good.id" :quantity="good.quantity" :key="good.id" />
    </ol>

    <div class="check-out-area" v-if="CartList.length > 0">
      <a class="btn btn-cart btn-cart-pay">{{ TotalPrice }}</a>
      <a class="btn btn-cart btn-cart-clear" @click="ClearCart">清空購物車</a>
    </div>
    <div class="cart-empty" v-if="CartList.length <= 0">
      <h5 class="cart-noItem">您的購物車沒有商品</h5>
      <a class="btn" @click="GoShopping">現在去選購</a>
    </div>
  </main>
</template>

<script>
import CartItem from '@/components/CartItem.vue'

export default {
  components: {
    CartItem,
  },
  computed: {
    CartList() {
      return this.$store.state.cart.items
    },
    TotalPrice() {
      let total = this.$store.getters['cart/CartTotalPrice']
      return `立即購買 $${total}`
    },
  },
  methods: {
    ClearCart() {
      this.$store.dispatch('cart/Clear')
    },
    GoShopping() {
      this.$router.push({ name: 'Order' })
    },
  },
}
</script>

<style lang="scss">
.page-cart {
  width: 95%;
  min-height: calc(100vh - 50px);
  margin: auto;
  padding-top: 50px;
  box-sizing: border-box;
}

.cart-list {
  width: 90%;
  margin: auto;
  border-bottom: solid 1px $color-black;
}

.check-out-area {
  width: 60%;
  margin: 20px auto;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;

  @include md {
    flex-flow: row nowrap;
  }

  .btn-cart {
    width: 70%;
    margin: 10px 0;
    color: $color-white;
    @include font-setting($font-size-small, $font-weight-bold);

    @include md {
      width: 30%;
      margin: 20px;
    }
  }
  .btn-cart-pay {
    background-color: $color-pink;
  }
  .btn-cart-clear {
    background-color: $color-gray;
  }
}

.cart-empty {
  margin: 20px 0;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;

  a {
    margin: 20px 0;
  }
}
</style>
