<template>
  <li class="cart-item container">
    <div class="item-pic">
      <img :src="img" />
    </div>
    <div class="item-title">
      <h6>{{ title }}</h6>
    </div>
    <div class="item-price">
      <span>單價 ${{ price }}</span>
    </div>
    <div class="item-quantity">
      <ButtonQuantity ref="quantity" @inputQuantity="GetInputQuantity" :quantity="quantity" />
    </div>
    <div class="item-total">
      <span>{{ Total }}</span>
    </div>
  </li>
</template>

<script>
import ButtonQuantity from '@/components/ButtonQuantity'
export default {
  components: {
    ButtonQuantity,
  },
  props: {
    id: { type: Number },
    quantity: { type: Number },
  },
  data() {
    return {
      title: '',
      price: '',
      img: '',
      inputQuantity: 1,
    }
  },
  computed: {
    Total() {
      return `$${this.inputQuantity * this.price}`
    },
  },
  methods: {
    GetInputQuantity(value) {
      this.inputQuantity = value

      let item = { id: this.id, quantity: value }

      if (value > 0) {
        this.$store.dispatch('cart/ItemEdit', item)
      } else {
        if (confirm('當物品數量少於1，將會移出購物車，是否移出？')) {
          this.$store.dispatch('cart/ItemRemove', this.id)
        } else {
          this.$refs.quantity.QuantityReset()
        }
      }
    },
  },

  mounted() {
    const { title, price, img } = this.$store.getters['products/ProductInfo'](this.id)
    this.title = title
    this.price = price
    this.inputQuantity = this.quantity
    this.img = require(`../assets/${img}`)
  },
}
</script>

<style lang="scss">
.container {
  display: grid;
  grid-template-areas:
    'img title quantity'
    'img price total';
  grid-template-columns: 100px 1fr 1fr;
  grid-template-rows: 50px 50px;
  gap: 0px 5px;
  justify-items: center;
  align-items: center;

  @include md {
    grid-template-areas: 'img price title quantity total';
    grid-template-columns: minmax(0px, 100px) 1fr 1fr 1fr 0.5fr;
    grid-template-rows: 1fr;
  }
}

.cart-item {
  width: 100%;
  min-width: 300px;
  margin: 20px auto;
  box-sizing: border-box;

  .item-pic {
    grid-area: img;
    justify-self: stretch;
    align-self: stretch;
  }
  .item-title {
    grid-area: title;
    justify-self: start;
    align-self: start;

    @include md {
      justify-self: center;
      align-self: center;
    }
  }
  .item-price {
    grid-area: price;
    justify-self: start;
    align-self: end;

    @include md {
      justify-self: center;
      align-self: center;
    }

    span {
      line-height: 0;
      color: $color-gray;
    }
  }
  .item-quantity {
    grid-area: quantity;
    justify-self: end;
    align-self: end;

    @include md {
      justify-self: center;
      align-self: center;
    }
  }
  .item-total {
    grid-area: total;
    justify-self: end;
    align-self: end;

    @include md {
      justify-self: center;
      align-self: center;
    }

    span {
      line-height: 0;
    }
  }
}
</style>
